import { Component, OnInit } from '@angular/core';
import {  Router, ActivatedRoute } from '@angular/router';
import { CustomersDataService } from '../../../../../costumers/customers-data-service';
import { Observable, of } from 'rxjs';
import { HallType } from '../../../../../costumers/hall-type';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { HttpHeaders } from '@angular/common/http';
import { HttpService } from '../../../../../http.service';
import { find, tap } from 'rxjs/operators';
import { CanDeactivateComponent } from '../../../../../can-deactivate-guard.service';
import { FormProccesorService } from '../../../../../costumers/form-proccesor.service';
//  declare let $:any;

@Component({
  selector: 'app-basic-edit',
  templateUrl: './basic-edit.component.html',
  styleUrls: ['./basic-edit.component.css']
})
export class BasicEditComponent implements OnInit, CanDeactivateComponent {

  phoneNum: any = '^((?=(02|03|04|08|09))[0-9]{2}[0-9]{3}[0-9]{4}|(?=(05|170|180))[0-9]{3}[0-9]{3}[0-9]{4})';
  emailPatteren: string = '^[a-z]+[a-zA-Z_\\d]*@[A-Za-z]{2,10}\.[A-Za-z]{2,3}(?:\.?[a-z]{2})?$';
  /***************  ********************/
  costumer: HallType;
  
  /* **************************** */
  private apiKey;
  addCostumerForm: FormGroup;

  isTrue: Observable<boolean>;
  masseges:any = [];
  private input: FormData = new FormData();
  

  constructor(private router: Router, 
      private halls: CustomersDataService, 
      private http: HttpService,
      private valForm: FormProccesorService) { }

  ngOnInit() {

    this.halls.costumerObsever.pipe(
      find((val) =>  {return val['customer']['id']})
      ).subscribe(cost => {
        let co = cost['customer'];
    
        let cId = (co && co["user_id"]) ? co["user_id"] : false;
        let authUser:any = this.http.authUser;
        let uId = authUser? authUser["id"] : false;

      // console.log('costumerId: '+ cId + " userId "+ uId);
      if(cId === uId){
        this.costumer = co;
        this.apiKey = this.http.getApiKey();
        this.formInt();
        this.isTrue = of(true);
      }else{
        // let state = decodeURIComponent(this.router.url).split("/");
        // let media = "/"+state[1]+"/"+cost["company"]+"/media";
        this.isTrue = of(false);
      }

    });
  }

  update(costumer){
    
    let comp = costumer['id'];
    let controls = this.addCostumerForm.controls[comp];
    let items = this.valForm.validate(controls,this.costumer,comp);
    
    if(items['status']){
      this.send(items['success']);
    }else{
      this.masseges = items['errors'];
      this.valForm.resetMessages().then(res => {
        
        this.masseges = res;
      });
    }
  }

  inputReset(costumer){
    let comp = costumer['id'];
    this.addCostumerForm.controls[comp].reset();
  }

  default(costumer){
    let comp = costumer.id;
    this.addCostumerForm.controls[comp].setValue(this.costumer[comp])
  }

  canDeactivate(): Observable<boolean> | Promise<boolean> | boolean {
    
    if(this.addCostumerForm.dirty || this.addCostumerForm.touched){
      return confirm("   .      ?")
    }else{
      return true;
    }
  }

  textAreaAdjust(o) {
    let target = o.target;
    target.style.height = "1px";
    target.style.height = (25 + target.scrollHeight) + "px";
  }

  textAreamouseleave(o) {
    let target = o.target;
    target.style.height = "1px";
    target.style.height = ((target.scrollHeight) - 50 + '%') + "px";
  }

  get f() { return this.addCostumerForm.controls; }

  private formInt() {

    this.addCostumerForm = new FormGroup({
      'company': new FormControl(this.costumer.company, [Validators.required]),
      // 'businessType': new FormControl(this.costumer.businessType, [Validators.required]),
      'title': new FormControl(this.costumer.title, [Validators.required]),
      'contact': new FormControl(this.costumer.contact, [Validators.required]),
      'tel': new FormControl(this.costumer.tel, [Validators.required]),
      'email': new FormControl(this.costumer.email, [Validators.required]),
      'address': new FormControl(this.costumer.address, [Validators.required]),
      'discription': new FormControl(this.costumer.discription, [Validators.required])
    });
  }

  onSubmit() {

    // this.masseges = [];
    let controls = this.addCostumerForm.controls;
    
    let items = this.valForm.validate(controls,this.costumer);
    //if(items['status'] === false) this.masseges.push(items['errors']);
    let success = items['success'];
    console.log(success);

    if(success){
      this.send(success);
    }else if(! items['status'] && ! success){
      this.masseges = items['errors'];

      this.valForm.resetMessages().then(res => {
        this.masseges = res;
      });
    } 
  }

  reset() {
    this.addCostumerForm.reset();
  }
  
  close() {
    this.router.navigate(['../']);
  }

  allTodefault(){

    let controls = this.addCostumerForm.controls;
    
    for(let ii in controls){
      if(controls.hasOwnProperty(ii)){
        if(controls[ii].value !== this.costumer[ii]){
          if(this.costumer[ii]) controls[ii].setValue(this.costumer[ii]);
        }
      }
    }
  }

  send(body){

    let updaterUrl = "http://ethio:8080/api/costumers/" + this.costumer["id"]+ "? _method=PATCH";
    // let updaterUrl = "http://ethio:8080/api/costumers/" + this.costumer["id"]+ "? _method=PUT";
    this.input.append('formInputs', JSON.stringify(body));
    
    this.halls.patchData(updaterUrl, this.input, {
      headers: new HttpHeaders({
        // 'Content-Type':  'application/json',
        'Authorization': 'Bearer ' + this.apiKey
      })
    })
    .subscribe(evt => {
      
      let msgs = this.valForm.getMassages(evt);
      this.masseges = msgs;
      this.valForm.resetMessages().then(res => {
        this.masseges = res;
      });
      console.log(msgs);
    }, (err) => {
      
      console.log(err);
      if (err["status"] === 401) {
        this.http.nextIslogged(false);
          window.sessionStorage.removeItem('user_key');
          // this.router.navigate(["/login"]);
          this.http.allowLogIn.next(true);
          // this.reset();
      }
    });
  }
}
