import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Observable, of } from 'rxjs';
import { HallType } from '../../../../../costumers/hall-type';
import { HttpHeaders } from '@angular/common/http';
import { CustomersDataService } from '../../../../../costumers/customers-data-service';
import { HttpService } from '../../../../../http.service';
import { find, first, toArray } from 'rxjs/operators';
import { CanDeactivateComponent } from '../../../../../can-deactivate-guard.service';
import { FormFilesProccesorService } from 'src/app/costumers/form-files-proccesor.service';
import { FormProccesorService } from 'src/app/costumers/form-proccesor.service';

declare var $: any;

@Component({
  selector: 'app-media-edit',
  templateUrl: './media-edit.component.html',
  styleUrls: ['../../../../../styles/style.component.css']
  // styleUrls: ['./media-edit.component.css']
})
export class MediaEditComponent implements OnInit, CanDeactivateComponent {

  /* **************************** */
  costumer: HallType;
  galleries: any;
  videos: any;

  /* **************************** */
  private apiKey;
  private guard = [];
  private arrayFlies: any = [];
  private masseges:any = [];
  private filesSize: number = 0;
  private existesFiles = [];
  private fileToDelete = [];
  private filesDl:{} = {
    'loggo': [],
    'video': [],
    'galleries': []
  };

  isTrue: Observable<boolean>;

  constructor(private router: Router, private halls: CustomersDataService, private http: HttpService, private formFiles: FormFilesProccesorService, private valForm:FormProccesorService) { }

  ngOnInit() {

    this.halls.costumerObsever.pipe(
      find((val) =>  {return val['customer']['id']})
      ).subscribe(cost => {
        let co = cost['customer'];
        let gal = cost['gallery'];
        
      let cId = (co && co["user_id"]) ? co["user_id"] : false;
      let uId = this.http.authUser["id"];
        
      if(cId === uId){
        this.galleries = gal['image'];
        this.videos = gal['video'];
        
        this.costumer = co;
        this.apiKey = this.http.getApiKey();
        this.isTrue = of(true);
        this.galleryInit();
        
      }else{
        this.isTrue = of(false);
      }

    });
  }
  galleryInit(){

    let imgs = this.formFiles.createFilesOb(this.galleries);
    let vid = this.formFiles.createFilesOb(this.videos);
    let loggo = this.formFiles.createFilesOb([this.costumer.loggo]);

    imgs.then(res => {
      this.selectedFiles(res,'galleries');
    });
    vid.then(res => {
      this.selectedFiles(res,'video');
    });
    loggo.then(res => {
      this.selectedFiles(res,'loggo');
    });
  }

  canDeactivate(): Observable<boolean> | Promise<boolean> | boolean {
    /* let resBool = this.http.isLogedIn.pipe().toPromise().then(res => {
        console.log(res);
        
    }); */

    if(this.filesSize >= 1 || this.arrayFlies.length >= 1 || this.arrayFlies.guard >= 1){
      return confirm("   .      ?");
    }else{
      return true;
    }
    
  }

  
  galUpdate(elem){
    
    let files = elem.files;
    if(!files[0]){
      
      let elmessage = {};
      elmessage[elem.id] = [{
          [elem.id]: elem.id + "     .",
          type: 'warning'
        }];
      
      this.masseges = elmessage;
      this.valForm.resetMessages().then(res => {
        
        this.masseges = res;
      });
      return;
    }
    let filesInput = this.formFiles.handelInputFiles(files,this.costumer,this.filesDl,this.existesFiles);
    let filesToSend = filesInput['toSend'];
    let method = (filesToSend['toUpdate'].length > 1)? "PUT":"PATCH";
    let fd = filesToSend['toDelete'];
    let input = filesInput['inputs'];

    if(fd.length > 0) input.append('filesToDelete', JSON.stringify(fd))
    this.send(input,method);
    // this.filesDl['galleries'] = [];
    // console.log(this.checkFileToDelete(files[0].target));
    console.log(filesToSend);
    
  }
  
  galReset(item){

    // let el = item.getElementsByTagName('A');
    let childrens = item.querySelectorAll('A');
    for(let ii = 0, len = childrens.length; ii < len; ii++){
      this.unSelectFiles(childrens[ii]);
    }
    
  }

  galDefault(galType){
    let gal = galType === "galleries"? this.formFiles.createFilesOb(this.galleries): 
        galType === "loggo"? this.formFiles.createFilesOb([this.costumer.loggo]):  
        galType === "video"? this.formFiles.createFilesOb(this.videos) : null;

        gal.then(res => {
          this.selectedFiles(res,galType);
        });
  }
  destroy(item){
    console.log(item);
    let el = item.querySelector('A');
    this.unSelectFiles(el);
  }

  onSubmit() {
    
    this.masseges = [];
    let err = {
      errors: null
    };

    let handelFiles = this.formFiles.handelInputFiles(this.arrayFlies,this.costumer,this.filesDl,this.existesFiles);
    let filesToSend = handelFiles['toSend'];
    let input = handelFiles['inputs'];
    let toDelete = filesToSend['toDelete'];
    let fSize = filesToSend['size'];
    let checkFilesSizes = (Math.round(fSize / Math.pow(1024, 2)) > 6) ? true : false;

    // console.log(this.filesSize + " : " +Math.round(this.filesSize / Math.pow(1024,2) ));
    // console.log(this.filesSize + " : " +Math.round(Math.pow(1024,2)* 6 ) / (Math.pow(1024,2)));

    // if(files['imgs'].length < 3) err['gallery'] = "     3 " ;
    if(checkFilesSizes) err['errors'] = ({
      'files': [{
        files: this.formFiles.formatBytes(fSize) + " : " + "   .",
        type: 'warning'
      }]
    });
    // if(typeof files['loggo'] === 'undefined' || ! files['loggo']) err['loggo'] = "      .";
    // if(typeof files['video'] === 'undefined' || ! files['video']) err['video']  = "    .";

    let haveFile = filesToSend['toUpdate'].length > 0;
    if (! err['errors'] && haveFile) {
      
      input.append('filesToDelete', JSON.stringify(toDelete));
      this.send(input,'PUT');

    }else{

      let filesErrors = err['errors'] ?this.valForm.getMassages(err):err['errors'];
      if(err['errors']){
        this.masseges = filesErrors;
        this.valForm.resetMessages().then(res => {
          this.masseges = res;
        });
      } 
    }
  }

  selectedFiles(event, elemTarget) {

    let files = event.target;
    let targetElement = $('input.'+elemTarget)[0].parentElement.nextElementSibling;
    
    files = files && files.files?files.files:event;

    if (! files[0] || this.guard.indexOf(elemTarget) >= 0) {
      console.log("You cant uplaod twise " + elemTarget);
      console.log('this.guard');
      console.log(this.guard);
      return;
    }
    if (elemTarget !== "galleries" && this.guard.indexOf(elemTarget) == -1) this.guard.push(elemTarget);
    

    // let videoType = ['video/3gpp', 'video/H261', 'video/H263', 'video/H264', 'video/JPEG', 'video/mp4', 'video/mpeg'].indexOf(files[0].type);
    // let imageType = ['image/png', 'image/jpeg', 'image/gif'].indexOf(files[0].type);
    for (let theFile of files) {

      let elemName = theFile.name.split('.')[0];
      theFile.id = elemName + '_' + theFile.size;
      theFile.target = elemTarget;

      if(theFile.exisst) this.existesFiles.push(theFile.id);

      if (! this.formFiles.fileContains(this.arrayFlies, theFile)) {
        
        this.formFiles.filseReader(theFile).then(res => {

          let elemOb = this.formFiles.createElements(res);
          elemOb['atag'].addEventListener("click", this.unSelectFiles.bind(this));

          this.filesSize += elemOb['theElem'].size;
          this.arrayFlies.push(elemOb['theElem']);
          targetElement.appendChild(elemOb['elemDiv']);

        },
          (error) => {
            console.log(error);
          });
      }
    }//END for loop
  }

  unSelectFiles(evt) {

    let aTag = evt && evt.target? evt.target.parentElement:evt;
    // let aTag = (typeof tar != "string" && tar !== "undefined")? evt.target.parentElement:evt;
    // this.fileToDelete = [];

    let div = aTag.parentElement;
    let parent = div.parentElement;
    let childrens = parent.children;
    
    let index = aTag.getAttribute('data-target');
   
    if((this.existesFiles.indexOf(aTag.id) >= 0) && (this.fileToDelete.indexOf(aTag.id) == (- 1))){
      //this.fileToDelete.push(this.findElem(aTag.id, index));
      if((index == "loggo") || (index == "video") && this.filesDl[index].length === 0) this.filesDl[index].push(this.findElem(aTag.id, index));
      if(index == "galleries" && this.filesDl[index].indexOf(aTag.id) == (- 1)) this.filesDl[index].push(this.findElem(aTag.id, index));
    } 
    
    for (let ii = 0, len = childrens.length; ii < len; ii++) {

      if (childrens[ii] && aTag.id === childrens[ii].id) {
        let subtractSize = childrens[ii].id.split('_');
        let posSubtrat = subtractSize[subtractSize.length - 1];
        // console.log(index);
        index = ((index == "loggo") || (index == "video")) ? this.guard.indexOf(index) : null;
        
        this.arrayFlies = this.formFiles.removeItem(this.arrayFlies, childrens[ii]);

        if (index === 0 || index >= 0) {
          this.guard.splice(index, 1);
        }

        this.filesSize -= posSubtrat;
        parent.removeChild(childrens[ii]);
        break;
      }
    }
  }

  private findElem(id, target){
    
    let ob = this.arrayFlies.find(elem => elem['id'] == id);
        //ob = ob? ob: this.arrayFlies.find(elem => elem['id'] == id);
    let item = (ob && target == 'galleries')? this.galleries.find(el => el.indexOf(ob.name) >= 0):
              (target == 'video')? this.videos.find(el => el.indexOf(ob.name) >= 0):(target == 'loggo')? this.costumer.loggo:null;
    return item;
  }
 
  
  reset() {
    location.reload();
  }
  close() {
    this.router.navigate(['../']);
  }

  send(body,method){

    let updaterUrl = "http://ethio:8080/api/costumers/" + this.costumer["id"]+ "? _method="+ method;
    // let updaterUrl = "http://ethio:8080/api/costumers/" + this.costumer["id"]+ "? _method=PUT";
    
    this.halls.patchData(updaterUrl, body, {
      headers: new HttpHeaders({
        // 'Content-Type':  'application/json',
        'Authorization': 'Bearer ' + this.apiKey
      })
    })
    .subscribe(evt => {
      
      console.log(evt);
      let msgs = this.valForm.getMassages(evt);
      this.masseges = msgs;
      console.log(msgs);

      this.valForm.resetMessages().then(res => {
        this.masseges = res;
      });

    }, (err) => {
      
      if (err["status"] === 401) {
        this.http.nextIslogged(false);
          window.sessionStorage.removeItem('user_key');
          window.location.reload();
          // this.http.allowLogIn.next(true);
      }
    });
  }
  private sendPatch(posterUrl, input){
    this.halls.patchData(posterUrl,
      input, {
        headers: new HttpHeaders({
          'Authorization': 'Bearer ' + this.apiKey
        })
      })
      .subscribe(evt => {
        console.log(evt);
        // this.masseges.push(evt["massege"]);

        setTimeout(() => {
          // this.router.navigate(["/halls_events"]);
        }, 5000);

      }, (err) => {
        
        if (err["status"] === 401) {
          this.http.nextIslogged(false);
          window.sessionStorage.removeItem('user_key');
          // this.router.navigate(["/login"]);
          this.http.allowLogIn.next(true);
          // this.reset();
        }
      });
  }
}
