import { Component, OnInit, OnDestroy, EventEmitter } from '@angular/core';
// import { Location } from '@angular/common';
// import { PagesService } from '../../../pages-service';
import { Router, RoutesRecognized, ActivatedRoute, Data } from '@angular/router';
import { map, filter, tap } from 'rxjs/operators';
import { CustomersDataService } from '../../../costumers/customers-data-service';
import { HallType } from '../../../costumers/hall-type';
import { Observable, of } from 'rxjs';

@Component({
  selector: 'app-halls',
  templateUrl: './halls.component.html',
  styleUrls: ['./halls.component.css'],
  providers:[]
})

export class HallsComponent implements OnInit, OnDestroy {

  
  path: boolean = true;
  urlUnsubscribe: any;
  hallsProps: Observable<HallType[]>;

  constructor(private router: Router, private route: ActivatedRoute,private halls: CustomersDataService ) {}

  ngOnInit() {
    
    this.route.data.subscribe(data => {
      // console.log(data['halls']);
       this.hallsProps =  data['halls']? of(data['halls']):null;
       
    });

    this.getCurrentUrl();
  }

  private getCurrentUrl(){

    let urlSliced = decodeURIComponent(window.location.pathname).slice(1);
    let urlPath = (urlSliced === 'halls-events' || urlSliced === 'app/halls-events')? true : false;

      this.urlUnsubscribe = this.router.events.pipe(
        filter((evt) => evt instanceof RoutesRecognized),
        map(_evt => decodeURIComponent(_evt["url"]).slice(1)),
      ).subscribe(evt => {
        this.path = (evt === 'halls-events' || evt === 'app/halls-events') ? true : false;
      });
    this.path = urlPath;
  }

  onSelectedLink(costumer:HallType){
    //this.halls.costumerEmit(costumer);
  }

  ngOnDestroy() {
    this.urlUnsubscribe.unsubscribe();
  }

}
