import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { HallResolver } from './halls-resolver.service';
import { RouteGuardService } from '../../../route-guard.service';
import { CanDeactivateGuardService } from '../../../can-deactivate-guard.service';

import { HallsComponent } from './HallsComponent';
import { HallComponent } from './hall/hall.component';
import { HallMediaComponent } from './hall-media/hall-media.component';
import { HallAboutComponent } from './hall-about/hall-about.component';
import { HallEditComponent } from './hall-edit/hall-edit.component';
import { AllEditComponent } from './hall-edit/all-edit/all-edit.component';
import { MediaEditComponent } from './hall-edit/media-edit/media-edit.component';
import { BasicEditComponent } from './hall-edit/basic-edit/basic-edit.component';
import { CommonModule } from '@angular/common';


const hallsRouting: Routes = [

  { path: "halls-events", component: HallsComponent, resolve: { halls: HallResolver },
    children: [
      {
        path: ":id", component: HallComponent, children: [
          { path: "media", component: HallMediaComponent },
          { path: 'about', component: HallAboutComponent },
          {
             path: "edit", component: HallEditComponent, canActivate: [RouteGuardService] ,
             children: [
              { path: "all", component: AllEditComponent, canDeactivate: [CanDeactivateGuardService] },
              { path: "gallery", component: MediaEditComponent, canDeactivate: [CanDeactivateGuardService] },
              { path: "basic", component: BasicEditComponent, canDeactivate: [CanDeactivateGuardService] }
             ]
          }
        ]}
    ],
  }
];

@NgModule({
  imports: [
    CommonModule,
    RouterModule.forChild(hallsRouting)
  ],
  exports: [RouterModule]
})
export class HallsRoutingModule { }
