import { Component, OnInit, Input } from '@angular/core';
import { CustomersDataService } from '../../../../costumers/customers-data-service';
import { of, Observable } from 'rxjs';
import { first } from 'rxjs/operators';

@Component({
  selector: 'app-hall-about',
  templateUrl: './hall-about.component.html',
  styleUrls: ['./hall-about.component.css']
})
export class HallAboutComponent implements OnInit {
  customer$: Observable<{}>;
  constructor(private hall: CustomersDataService) { }

  ngOnInit() {
    
    this.hall.costumerObsever.pipe(first()).subscribe(
      (dt) => {
        this.customer$ = of(dt['customer']);
        console.log(dt['customer']);
        
      });
  
  }
}
